.check-box {
    background-color: black;
    position: fixed;
    width: 100%;
    color: #fff;
    height: 100%;
    .check-btn {
        display: block;
        text-decoration: none;
        color: #fff;
        cursor: pointer;
        position: absolute;
        top: 50vh;
        height: 100px;
        width: 100px;
        border: 1px solid;
        border-radius: 50%;
        text-align: center;
        padding-top: 30px;
        left: 50vw;
        margin: -50px 0 0 -50px;
        background-color: #00ab26;
        box-shadow: 2px 3px 5px #bfbfbf;
        font-size: 30px;
        transition: all 1s;
        em {
            font-size: 12px;
            margin-top: 42px;
            display: block;
            font-style: normal;
            transition: all 1s;
        }
        &:hover {
            height: 150px;
            width: 150px;
            margin: -75px 0 0 -75px;
            background-color: #02801b;
            box-shadow: 5px 6px 8px #bfbfbf;
            font-size: 40px;
            padding-top: 50px;
            em {
                margin-top: 62px;
            }
        }
    }
    &.checked {
        background-color: #fff;
        color: #333;
        .check-btn {
            background-color: #fa2104;
            box-shadow:inset 0 0 30px #444;
            color: #333;
        }
    }
}